import React from 'react';


export default class Projet extends React.Component {
	constructor(props) {
		super(props);
		this.ref = React.createRef();
		this.handleMouseEnter = this.handleMouseEnter.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
	}

	handleMouseEnter() {
		const label = this.ref.current.querySelector('a p');
		label.style.opacity = '100%';
	}

	handleMouseLeave() {
		const label = this.ref.current.querySelector('a p');
		label.style.opacity = '0%';
	}

	render() {
		return (
			<div ref={this.ref} className="projet">
				<h1>{this.props.title}</h1>
				<div className="projet-content">
					<a 
						href={this.props.link}
						onMouseEnter={this.handleMouseEnter}
						onMouseLeave={this.handleMouseLeave}
					>
						<img src={this.props.imageURL} />
						<p>Voir le projet</p>
					</a>
					<p className="projet-description">
						{this.props.description}
						<br/><br/>
						<a className="button" href={this.props.link}>Voir le projet</a>
					</p>
				</div>
			</div>
		)
	}
}
